.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  /* background-color: #e3e0d9; */
  background: linear-gradient(-225deg, #cbf0f0 0%, #dbdade 56%, #ffdee2 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(40, 40, 40);
}

.App-link {
  color: #61dafb;
}

.favicon-code {
  width: 80vw;
  padding: 4px;
  height: 120px;
  overflow: scroll;
  overflow-x: hidden;
  text-align: left;
  font-size: 12px;
  background-color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
